import React from 'react';
import showcaseData from './data/showcase.json';

type Showcase = {
    link: string;
    title: string;
}

export default function Showcase() {
    return (
        <>
            <h2>Projects</h2>
            <div className={'grid'}>
                {
                    showcaseData.map((v: Showcase, i: number) => {
                        return (
                            <a href={v.link}
                               key={`showcase-${i}`}
                               className={'card'}
                            >
                                <h2>{v.title} &rarr;</h2>
                            </a>
                        );
                    })
                }
            </div>
        </>
    );
}
